.new{
    &__container {
        row-gap: 2rem;
    }
    &__mens {
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: var(--dark-color-lighten);
        border-radius: .5rem;
        padding: 2rem;
    }
    &__mens-img {
        width: 276px;
        margin-bottom: var(--mb-3);
    }
    &__title {
        font-size: var(--bigger-font-size);
        margin-bottom: .25rem;
    }
    &__preci {
        display: block;
        margin-bottom: var(--mb-3);
    }
    &__sneaker {
        display: grid;
        gap: 1.5rem;
        grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    }
    &__sneaker-card {
        position: relative;
        padding: 3.5rem 1.5rem;
        background-color: var(--dark-color-lighten);
        border-radius: .5rem;
        overflow: hidden;
        display: flex;
        justify-content: center;
    }
    &__sneaker-img {
        width: 220px;
    }
    &__sneaker-overlay {
        position: absolute;
        left: 0;
        bottom: -100%;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(138,138,138,.3);
        transition: .3s;
    }
    &__sneaker-card:hover .new__sneaker-overlay{
        bottom: 0;
    }
}

@media screen and (min-width: 576px) {
    .new__mens {
        align-items: center;
    }
}

@media screen and (min-width: 1024px) {
    .new{
        &__container {
            grid-template-columns: max-content 1fr;
        }
        &__mens {
            align-items: initial;
            justify-content: flex-end;
            padding: 4rem 2rem;
        }
        &__mens-img {
            margin-bottom: var(--mb-6);
        }
    }
}